
.player-container {
    width: 40%;
    margin: 0 auto;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    padding: 20px;
}

.player-name {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
    color: #333;
}

.table-container {
    overflow-x: auto;
}

.player-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
}

.player-table thead {
    background-color: #333;
    color: #fff;
}

.player-table th, .player-table td {
    padding: 10px;
    text-align: center;
    border-bottom: 1px solid #ddd;
}

.player-table tbody tr:nth-child(odd) {
    background-color: #f9f9f9;
}

.player-table tbody tr:nth-child(even) {
    background-color: #fff;
}

.randomize-button {
    display: block;
    width: 150px;
    margin: 0 auto;
    padding: 10px;
    background-color: #333;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.randomize-button:hover {
    background-color: #555;
}

