.guess-container {
    width: 60%;
    margin: 20px auto;
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  }
  
  .guess-form {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    gap: 10px;
  }
  
  .guess-input {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ddd;
    border-radius: 5px;
    width: 300px;
  }
  
  .guess-button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #333;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .guess-button:hover {
    background-color: #555;
  }
  
  .guess-result {
    margin-top: 20px;
    text-align: center;
    font-size: 18px;
    font-weight: bold;
  }
  
  .correct {
    color: #238823;
  }
  
  .incorrect {
    color: #D2222D;
  }

  .close {
    color: #FFBF00;
  }
  
  .points-display {
    margin-top: 10px;
    font-size: 16px;
    text-align: center;
  }
  
  .randomize-button {
    margin-top: 20px;
    padding: 10px;
    background-color: #333;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .randomize-button:hover {
    background-color: #555;
  }

.start-game-button {
  font-size: 150%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  padding: 10px;
  background-color: #333;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.start-game-button:hover {
  background-color: #555;
}


@keyframes shake {
  0% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  50% { transform: translateX(5px); }
  75% { transform: translateX(-5px); }
  100% { transform: translateX(0); }
}

.wrong-guess {
  animation: shake 0.5s;
}